

.wrapper5{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
   margin-top:8px ;
   /* background-color: #000000;
   border-radius: 20px; */
    /* margin-right: 5px; */
     -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    
  }
  
  @media(max-width:1000px) {
    .wrapper{
      display: none;
    }
  }
  .switch_box5{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 30px;
    border-radius: 20px;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
  }
  
  
  .box_3{
    background:  #26282A;
    /* background-color: #000000;  */
  
  }
  
  .toggle_switch5{
    height: 30px;
    width: 60px;
    position: relative;
  }
  
  input[type="checkbox"].switch_5{
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
    outline: 0;
    z-index: 1;
  }
  
  svg.checkbox .outer-ring{
    stroke-dasharray: 375;
    stroke-dashoffset: 375;
    -webkit-animation: resetRing .35s ease-in-out forwards;
            animation: resetRing .35s ease-in-out forwards;
  }
  
  input[type="checkbox"].switch_5:checked + svg.checkbox .outer-ring{
    -webkit-animation: animateRing .35s ease-in-out forwards;
            animation: animateRing .35s ease-in-out forwards;
  }
  
  input[type="checkbox"].switch_5:checked + svg.checkbox .is_checked{
    opacity: 1;
    -webkit-transform: translateX(0) rotate(0deg);
            transform: translateX(0) rotate(0deg);
  }
  
  input[type="checkbox"].switch_5:checked + svg.checkbox .is_unchecked{
    opacity: 0;
    -webkit-transform: translateX(-200%) rotate(180deg);
            transform: translateX(-200%) rotate(180deg);
  }
  
  
  svg.checkbox{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    
  }
  
  svg.checkbox .is_checked{
    opacity: 0;
    fill: yellow;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: translateX(200%) rotate(45deg);
            transform: translateX(200%) rotate(45deg);
    -webkit-transition: all .35s;
    transition: all .35s;
  }
  
  svg.checkbox .is_unchecked{
    opacity: 1;
    fill: #ededed;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: translateX(0) rotate(0deg);
            transform: translateX(0) rotate(0deg);
    -webkit-transition: all .35s;
    transition: all .35s;
  }
  
  @-webkit-keyframes animateRing{
    to{
      stroke-dashoffset: 0;
      stroke: #b0aa28;
    }
  }
  
  @keyframes animateRing{
    to{
      stroke-dashoffset: 0;
      stroke: #b0aa28;
    }
  }
  
  @-webkit-keyframes resetRing{
    to{
      stroke-dashoffset: 0;
      stroke: #c7c5c5;
      
    }
  }
  
  @keyframes resetRing{
    to{
      stroke-dashoffset: 0;
      stroke: #bdbdbd;
      
    }
  }